import React from 'react';
import styles from '../sass/work-section.module.scss'
import FilterItem from "./FilterItem";

const FilterList = (props) => {

    const filters = props.list.techs.map((tech, i) => {
        return (
            <FilterItem key={props.cat + i} name={tech.name} count={tech.projectIDs.length}
                        isActive={tech.name === props.activeFilter}
                        onFilter={(filter)=>props.onFilter(filter)}
            />
        );
    });

    return (
        <div className={styles.filtersContainer}>
            {filters}
        </div>
    );
};

export default FilterList;
