import React, {useEffect, useState} from "react";
import styles from "../../sass/custom.module.scss";
import CustomIcon from "./CustomIcon";

let id = 0;
let setState = null;

export const MSG_TYPES = {
    SUCCESS: 'Success',
    INFO: 'Info',
    WARNING: 'Warning',
    ERROR: 'Error',
}

const Colors = Array(Object.keys(MSG_TYPES).length);
Colors[MSG_TYPES.SUCCESS] = {bg: '#ccf8e4', title: '#67bb98', text: '#82a08c'};
Colors[MSG_TYPES.INFO] = {bg: '#c8ecff', title: '#4e8cad', text: '#688791'};
Colors[MSG_TYPES.WARNING] = {bg: '#fff3a5', title: '#b48e46', text: '#a29158'};
Colors[MSG_TYPES.ERROR] = {bg: '#ffe2df', title: '#de827f', text: '#9f7872'};

let MESSAGES = [];

export const ShowMsg = (msg, type) => {
    setState(prev => [...prev, {id: id++, msg, type}]);
};

const MessageCard = ({item, index}) => {
    const {id} = item;
    const type = item.type;

    const [styleClass, setStyleClass] = useState(styles.showMessageCard);

    const closeCard = () => {
        setStyleClass(() => styles.hideMessageCard);
        setTimeout(() => {
            setState(MESSAGES.filter((msg) => msg.id !== id));
        }, 600); // should be slightly less than animation duration
    }

    useEffect(() => {
        setTimeout(() => {
            closeCard();
        }, (4000 + index * 1000));
    }, [id]);

    return (
        <div className={`${styles.messageCard} ${styleClass}`} style={{backgroundColor: Colors[item.type].bg}}>
            <CustomIcon icon={type} size={20} className={styles.messageIcon} color={Colors[item.type].title} />
            <div className={styles.messageBody}>
                <span style={{color: Colors[item.type].title, fontWeight: 'bold', fontSize: '.9rem'}}>{type}</span>
                <span style={{color: Colors[item.type].text, fontSize: '.8rem'}}>{item.msg}</span>
            </div>
            <CustomIcon icon={'Close'} size={12} className={styles.closeIcon} color={Colors[item.type].title}
                        onClick={()=> closeCard()}/>
        </div>
    )
}

const MessageContainer = () => {
    const  [activeMessages, setActiveMessages] = useState([]);

    useEffect(() => {
        setState = setActiveMessages;
        MESSAGES = activeMessages;
    }, [activeMessages]);

    const messages = activeMessages.map((item, index) => <MessageCard key={item.id} index={index} item={item} />);

    return (
        <div className={styles.messageContainer}>
            {messages}
        </div>
    )
};

export default MessageContainer;
