import React from 'react'
import styles from '../sass/home.module.scss'
import WorkSection from "../components/WorkSection";
import Footer from "../components/Custom/Footer";
import CustomButton from "../components/Custom/CustomButton";
import AppBar from "../components/AppBar";
import Navigation from "../pages/navigation";
import {useNavigate} from "react-router-dom";
import Container from "../components/Container";
import workCategories from "../data/js/workCategories";
import ContactFloating from "../components/Custom/ContactFloating";
import INFO from "../data/js/info";


const Home = () => {

    const navigate = useNavigate();

    return (
        <div>
            <ContactFloating src={Navigation.Home}/>
            <AppBar/>
            <Container>

                <div className={styles.infoContainer}>
                    {/*<img className={styles.bgImg} src={require("../assets/BG2.png")}/>*/}
                    <div className={styles.bg}/>
                    <div className={styles.info}>
                        <img className={styles.img} src={'assets/img.png'}/>

                        <div className={styles.infoTextContainer}>
                            <span className={styles.title}>{INFO.welcome} {window.innerWidth > 420 && <br/>} {INFO.title}</span>
                            <p className={styles.infoText}>{INFO.brief}</p>

                            <div className={styles.infoBtnContainer}>
                                <CustomButton text={"Contact me"} onClick={() => navigate(`/${Navigation.Contact}`)}/>
                                <CustomButton text={"Check my work"} reverse
                                              onClick={() => navigate(`/${Navigation.Work}`)}/>
                            </div>

                        </div>
                    </div>
                </div>
                <div className={styles.ribbon}/>

                <WorkSection src={Navigation.Home} workCategory={workCategories.personal}/>

                <Footer/>
            </Container>
        </div>
    )
};

export default Home;
