import React, {useEffect} from 'react';
import {useParams, useLocation} from "react-router-dom";
import Container from "../components/Container";
import Header from "../components/Custom/Header";
import Footer from "../components/Custom/Footer";
import Navigation from "./navigation";
import Title from "../components/Custom/Title";
import personalWorkData from "../data/js/personalWorkData";
import styles from "../sass/work.module.scss"
import WrongWorkId from "./WrongWorkID";
import DataCard from "../components/DataCard";
import TechList from "../components/Custom/TechList";
import workCategories from "../data/js/workCategories";
import academicWorkData from "../data/js/academicWorkData";
import WorkNavigator from "../components/Custom/WorkNavigator";
import PrevItemButton from "../components/Custom/PrevItemButton";

const Work = () => {
    const params = useParams();
    const {state} = useLocation();

    const work = (params.category === workCategories.personal)
        ? personalWorkData.find((el) => el.id == params.id)
        : academicWorkData.find((el) => el.id == params.id);

    const technicals = work.technical?.map((point, i) => {
        return (
            <p key={i}>{` ${point}`}</p>
        )
    })

    const dataCards = work?.data.map((el, i) => {
        return (
            (i % 2 === 0)
                ? <DataCard key={i} data={el}/>
                : <DataCard key={i} data={el} reverse/>
        );
    });

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [work])

    const headerBtn = (state != null) ? state.src : Navigation.Home;
    const nav = (state != null)
        ? (state.src === Navigation.Work)
            ? `/${Navigation.Work}/${params.category}`
            : `/${Navigation.Home}`
        : `/${Navigation.Home}`

    return (
        <Container>
            <Header btn={headerBtn} nav={nav}/>

            {
                (work === undefined) ? <WrongWorkId/> :
                    <div className={styles.dataContainer}>
                        <div className={styles.headerInfo}>
                            <Title title={work.name}/>

                            <div className={styles.techListContainer}>
                                <TechList list={work.techs} className={styles.briefTechs}
                                          itemStyle={styles.briefTechsItem}/>
                            </div>

                            <WorkNavigator category={params.category} id={work.id}/>

                            <p className={styles.workBrief}> {work.brief} </p>
                            <PrevItemButton externalUrl={work.externalUrl}/>


                        </div>

                        {work.technical && <div className={styles.techInfoContainer}>
                            <h2>Technical</h2>
                            {technicals}
                        </div>}

                        {dataCards}
                    </div>
            }

            <Footer/>
        </Container>
    );
};

export default Work;
