import React, {useMemo} from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

export const options = {
    indexAxis: 'y',
    elements: {
        bar: {
            borderWidth: 2,
        },
    },
    responsive: true,
    plugins: {
        legend: {
            display: false,
        },
    },
};

const HorizontalChart = ({title, data}) => {
    const chart = useMemo(() => {
        return ({
            labels: data.map(skill => skill.name),
                datasets: [
                    {
                        label: title,
                        data: data.map(skill => skill.value),
                        borderColor: 'rgb(255, 99, 132)',
                        backgroundColor: 'rgba(255, 99, 132, 0.5)',
                    },
                ],
        });
    }, [title, data]);

    return (
        <Bar options={options} data={chart} />
    )
}

export default HorizontalChart;
