import React, {useState, useEffect} from 'react';
import styles from "../../sass/custom.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faChevronLeft, faChevronRight} from "@fortawesome/free-solid-svg-icons";

const ImageViewer = (props) => {

    const [activeId, setActiveId] = useState(null);
    const {imgId} = props.data;

    const {isVisible} = props.data

    const list = props.data.imgs?.map((img, i) => {
        return (
            <img className={(i === activeId) ? styles.activeImg : ""} key={i} src={img}
                    onClick={() => setActiveId(i)}/>
        )
    });

    const onSwitchImg = (dir) => {
        const len = props.data.imgs.length;
        setActiveId((imgId) => {
            return (imgId + len + dir) % len
        })
    };

    useEffect(() => {
        setActiveId(imgId)
    }, [imgId])

    useEffect(() => {
        if (isVisible) {
            document.body.style.overflowY = "hidden"
        } else {
            document.body.style.overflowY = "scroll"
        }
    }, [isVisible])

    return (
        (isVisible)
            ?

            <div className={styles.imageViewer}>
                <div className={styles.backdrop} onClick={() => props.onDismiss()}/>

                <div className={styles.viewer}>

                    <button className={`${styles.btn} ${styles.left}`} onClick={()=> onSwitchImg(-1)}>
                        <FontAwesomeIcon icon={faChevronLeft}/>
                    </button>

                    <img src={props.data.imgs[activeId]}/>

                    <button className={`${styles.btn} ${styles.right}`} onClick={()=> onSwitchImg(1)}>
                        <FontAwesomeIcon icon={faChevronRight}/>
                    </button>

                </div>

                <div className={styles.browser}>
                    {list}
                </div>

            </div>

            : <></>
    );
};

export default ImageViewer;
