import React from 'react';
import styles from '../sass/work-section.module.scss'
import TechList from "./Custom/TechList";
import PrevItemButton from "./Custom/PrevItemButton";
import {useNavigate} from "react-router-dom";
import Navigation from "../pages/navigation";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faStar} from "@fortawesome/free-solid-svg-icons";
import ImageContainer from "./Custom/ImageContainer";

const PrevItem = (props) => {
    const navigate = useNavigate();

    const secPrev = props.secPrev.map((prev, i) => {
        return (
            <ImageContainer key={i + 1} className={styles.prevImgFloat} src={prev.img}
                            style={prev.pos} onPress={() => props.onImgSelect(i + 1)}/>
        )
    });

    return (
        <div className={styles.workItemContainer}>
            <div className={styles.workItem}>
                <div className={styles.clamp}>
                    <ImageContainer className={styles.prevImg} src={props.mainPrev} onPress={() => props.onImgSelect(0)}/>
                </div>
                <div className={styles.prevFloatContainer}>
                    {secPrev}
                </div>
            </div>

            <div className={styles.briefContainer}>
                <div className={styles.workTitleContainer}>
                    {props.isFeatured && <FontAwesomeIcon className={styles.featuredIcon} icon={faStar} />}
                    <h3 className={styles.workTitle}>{props.name}</h3>
                    {props.subTitle && <span className={styles.workSubTitle}> {`(${props.subTitle})`} </span>}
                </div>

                <TechList list={props.techs} className={styles.briefTechs} itemStyle={styles.briefTechsItem} limit={3}/>

                <p className={styles.workBrief}>{props.brief}</p>

                <PrevItemButton
                    onPreview={() => navigate(`/${Navigation.Work}/${props.category}/${props.id}`, {state: {src: props.src}})}
                    externalUrl={props.externalUrl}/>

            </div>
        </div>
    );
};

export default PrevItem;
