import React, {useState} from "react";
import styles from '../../sass/custom.module.scss'

const ImageContainer = ({className, style, src, onPress, customStyle = {}}) => {

    const [isLoading, setIsLoading] = useState(true);

    return (
        <>
            {isLoading && (
                <div className={className} style={{...style, ...customStyle}}>
                    <div className={styles.animatedBackground}/>
                </div>
            )}
            <img key={0} className={className} style={isLoading ? {...style, visibility: 'hidden'} : style} src={src}
                 onClick={() => onPress(0)} onLoad={() => setIsLoading(false)}/>
        </>
    )
};

export default ImageContainer;
