import React from 'react';
import Home from "./pages/Home";
import {Routes, Route} from "react-router-dom";
import Work from "./pages/Work";
import NotFound from "./pages/NotFound";
import Works from "./pages/Works";
import Navigation from "./pages/navigation";
import Contact from "./pages/Contact";
import Resume from "./pages/Resume";
import About from "./pages/About";

function App() {
    return (
        <div className="App">
            <Routes>
                <Route path="/">
                    <Route path={Navigation.Home} element={<Home/>}/>
                    <Route index element={<Home/>}/>
                    <Route path={Navigation.Work}>
                        <Route path=":category" element={<Works/>}/>
                        <Route path=":category/:id" element={<Work/>}/>
                        <Route index element={<Works/>}/>
                    </Route>
                    <Route path={Navigation.Contact} element={<Contact/>}/>
                    <Route path={Navigation.Resume} element={<Resume/>}/>
                    <Route path={Navigation.About} element={<About/>}/>
                </Route>
                <Route path="*" element={<NotFound/>}/>
            </Routes>
        </div>
    );
}

export default App;
