import React from 'react';
import styles from '../../sass/custom.module.scss'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faEye} from '@fortawesome/free-solid-svg-icons'

const PrevItemButton = (props) => {

    return (
        <div className={styles.prevItemBtnContainer}>

            {props.onPreview &&
                <div className={styles.prevBtn} onClick={() => props.onPreview()}>
                    <FontAwesomeIcon className={styles.prevIcon} icon={faEye}/>
                    <span>
                        Preview
                    </span>
                </div>
            }

            {(props.externalUrl != null) &&
                <a className={styles.urlBtn} href={props.externalUrl.url} target="_blank">
                    <FontAwesomeIcon className={styles.prevIcon} icon={props.externalUrl.type.icon}/>
                    <span>
                        {props.externalUrl.type.title}
                    </span>
                </a>
            }

        </div>
    );
};

export default PrevItemButton;
