import React from "react";
import styles from "../../sass/custom.module.scss";

const Paragraph = ({title, body}) => {

    console.log(body);

    const paragraphs = body.map((p, index) => {
        return (
            <p key={index} className={styles.paragraphBody}>{p}</p>
        )
    })

    return (
        <div className={styles.paragraph}>
            <h2>{title}</h2>
            {paragraphs}
        </div>
    )
};

export default Paragraph;
