import React from 'react';
import {faEnvelope} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import styles from '../../sass/custom.module.scss'
import {useNavigate} from "react-router-dom";
import Navigation from "../../pages/navigation";

const ContactFloating = (props) => {
    const navigate = useNavigate();

    return (
        <div className={styles.floatingBtn} onClick={()=> navigate(`/${Navigation.Contact}`, {state: {src: props.src, nav: props.nav}})}>
            <FontAwesomeIcon className={styles.icon} icon={faEnvelope} />
            <span>Contact me</span>
        </div>
    );
};

export default ContactFloating;
