import React from 'react';
import styles from "../sass/work.module.scss"
import Header from "../components/Custom/Header";
import Navigation from "./navigation";
import WrongWorkId from "./WrongWorkID";
import Title from "../components/Custom/Title";
import TechList from "../components/Custom/TechList";
import Footer from "../components/Custom/Footer";
import Container from "../components/Container";

const NotFound = () => {
    return (
    <Container>
        <Header btn={Navigation.Home}
                nav={`/${Navigation.Home}`}/>

        <div className={styles.wrongIdContainer}>
            <h1 className={styles.title404}>404</h1>
            <h1 className={styles.title}>Page Not Found</h1>
        </div>

        <Footer/>
    </Container>

    );
};

export default NotFound;
