import React from 'react';
import styles from "../sass/work.module.scss"
import {Orientation} from "../data/js/Constants";
import ImageContainer from "./Custom/ImageContainer";

const DataCard = (props) => {
    const {data} = props;

    return (
        <div className={styles.dataCardContainer + " " + ((props.reverse == null) ? styles.marRight : styles.marLeft)}>

            {(props.reverse == null) ?

                <>
                    <div className={styles.imgContainer}>
                        <ImageContainer src={data.img}
                            containerStyle={styles.imgContainer} customStyle={{position: 'absolute', width: '100%'}}
                            className={(data.orientation === Orientation.Vertical) ? styles.imgV : styles.imgH} />
                    </div>
                    <div className={styles.separator}/>
                    <div className={styles.dataTextContainer}>
                        <h2 className={styles.subTitle}>{data.subTitle}</h2>
                        <p>{data.text}</p>
                    </div>
                </>

                :

                <>
                    <div className={styles.dataTextContainer}>
                        <h2 className={styles.subTitle}>{data.subTitle}</h2>
                        <p>{data.text}</p>
                    </div>
                    <div className={styles.separator}/>
                    <div className={styles.imgContainer}>
                        <ImageContainer src={data.img}
                            containerStyle={styles.imgContainer} customStyle={{position: 'absolute', width: '100%'}}
                            className={(data.orientation === Orientation.Vertical) ? styles.imgV : styles.imgH} />
                    </div>
                </>
            }


        </div>
    );
};

export default DataCard;
