import React, {useRef, useState} from 'react';
import Header from "../components/Custom/Header";
import Navigation from "./navigation";
import styles from "../sass/contact.module.scss"
import Footer from "../components/Custom/Footer";
import Container from "../components/Container";
import Title from "../components/Custom/Title";
import CustomButton from "../components/Custom/CustomButton";
import {useLocation} from "react-router-dom";
import {faLinkedinIn} from "@fortawesome/free-brands-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import apiKey from '../emailkey'
import {sendForm} from "@emailjs/browser";
import {MSG_TYPES, ShowMsg} from "../components/Custom/MessageContainer";

const Contact = () => {
    const {state} = useLocation();

    const formRef = useRef(null);

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [subject, setSubject] = useState('');
    const [message, setMessage] = useState('');

    const [loading, setLoading] = useState(false);

    const validateEmpty = (name, field) => {
        if (field === ''){
            ShowMsg(`Field '${name}' can't be empty`, MSG_TYPES.ERROR);
            // TODO err => name cannot be empty
            return false;
        }

        return true;
    }

    const validateEmail = mail =>
    {
        if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail))
        {
            return true
        }
        ShowMsg(`Please enter a valid email`, MSG_TYPES.ERROR);
        // alert("You have entered an invalid email address!")
        return false
    }

    const handleSubmit = () => {
        if (loading) return;

        let isFilledData = validateEmpty('Name', name);
        isFilledData = validateEmpty('Email', email) && isFilledData;
        isFilledData = validateEmpty('Subject', subject) && isFilledData;
        isFilledData = validateEmpty('Message', message) && isFilledData;

        const isValidEmail = isFilledData && validateEmail(email);

        if (!isFilledData || !isValidEmail) return;

        ShowMsg(`Sending your message.`, MSG_TYPES.INFO);
        sendForm(apiKey.SERVICE_ID, apiKey.TEMPLATE_ID, formRef.current, apiKey.USER_ID)
            .then(() => {
                    ShowMsg(`Message received successfully. Thank you for contacting me.`, MSG_TYPES.SUCCESS);
                },
                () => {
                    ShowMsg(`We couldn't get you message, Please try again.`, MSG_TYPES.ERROR);
                })
            .finally(() => {
                setLoading(false);
                setName('');
                setEmail('');
                setSubject('');
                setMessage('');
            });
    };

    return (
        <Container>
            <Header btn={(state != null) ? state.src : Navigation.Home}
                    nav={(state != null) ? `/${state.src}` : `/${Navigation.Home}`}/>

            <Title title={"Contact"}/>

            {/*TODO Form validation ??*/}
            <form ref={formRef} className={styles.container}>

                <div className={styles.row}>
                    <input className={styles.name} placeholder={"Name"} name={'name'}
                           value={name} onChange={e => setName(e.target.value)} />

                    <input className={styles.email} type={"email"} placeholder={"Email"} name={'email'}
                           value={email} onChange={e => setEmail(e.target.value)}/>
                </div>

                <input className={styles.subject} placeholder={"Subject"} name={'subject'}
                       value={subject} onChange={e => setSubject(e.target.value)}/>

                <textarea className={styles.msg} placeholder={"Message"} name={'message'}
                          value={message} onChange={e => setMessage(e.target.value)} />

                <div className={styles.contactBtnContainer}>
                    <CustomButton text={"Send"} revers onClick={() => handleSubmit()}/>
                    <span className={styles.linkedInBtn}>
                        <FontAwesomeIcon icon={faLinkedinIn}/>
                        <a href={"https://www.linkedin.com/in/muhammad-sharafeldin97/"} rel="noreferrer"
                           target={"_blank"}>
                            LinkedIn
                        </a>
                    </span>
                </div>

            </form>

            <Footer/>
        </Container>
    );
};

export default Contact;
