import React from 'react';
import Header from "../components/Custom/Header";
import Navigation from "./navigation";
import Footer from "../components/Custom/Footer";
import Container from "../components/Container";
import Title from "../components/Custom/Title";
import INFO from "../data/js/info";
import styles from "../sass/about.module.scss"
import Paragraph from "../components/Custom/Paragraph";
import HorizontalChart from "../components/Custom/HorizontalChart";

const About = () => {

    const paragraphs = INFO.About.map((item, index) => {
        return (
            <Paragraph key={index} title={item.title} body={item.body} />
        )
    });

    const skills = INFO.Skills.map((item, index) => {
        return (
            <div className={styles.container}>
                <h3>{item.title}</h3>
                <HorizontalChart key={index} title={item.title} data={item.skills} />
            </div>
        )
    });

    return (
        <Container>
            <Header btn={"Home"} nav={`/${Navigation.Home}`}/>
            <Title title={"About me"}/>

            <div className={styles.container}>
                {paragraphs}
                {/*{skills}*/}
            </div>

            <Footer/>
        </Container>
    );
};

export default About;
