import React, {useRef} from 'react';
import styles from '../sass/resume.module.scss'
import HeaderBtn from "../components/Custom/HeaderBtn";
import Navigation from "./navigation";
import Header from "../components/Custom/Header";

const Resume = () => {



    return (
        <div className={styles.container}>

            {window.innerWidth > 1024 ? (
                <>
                    <div className={styles.section}>
                        <HeaderBtn btn={"Home"} className={styles.btn} nav={`/${Navigation.Home}`}/>
                    </div>
                    <div className={styles.sectionRibbon}/>
                </>
            ) : (
                <Header btn={"Home"} nav={`/${Navigation.Home}`}/>
            )}

            <iframe className={styles.pdf} frameBorder={0}
                    src={"assets/data/Resume.pdf"}/>
        </div>
    );
};

export default Resume;
