import React from 'react';
import styles from "../../sass/custom.module.scss";
import Navigation from "../../pages/navigation";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faChevronLeft, faChevronRight} from "@fortawesome/free-solid-svg-icons";
import {useNavigate} from "react-router-dom";
import workCategories from "../../data/js/workCategories";
import personalWorkData from "../../data/js/personalWorkData";
import academicWorkData from "../../data/js/academicWorkData";

const WorkNavigator = ({category, id}) => {
  const navigate = useNavigate();
  const maxId = (category === workCategories.personal)
    ? personalWorkData[personalWorkData.length - 1].id
    : academicWorkData[academicWorkData.length - 1].id;

  return (
    <div className={styles.navBtnContainer}>
      {id > 1 &&
        <button className={`${styles.btn} ${styles.backBtn}`}
                onClick={() => navigate(`/${Navigation.Work}/${category}/${id - 1}`)}>
          <FontAwesomeIcon icon={faChevronLeft}/>
        </button>
      }

      {id < maxId &&
        <button className={`${styles.btn} ${styles.nextBtn}`}
                onClick={() => navigate(`/${Navigation.Work}/${category}/${id + 1}`)}>
          <FontAwesomeIcon icon={faChevronRight}/>
        </button>
      }
    </div>
  );
};

export default WorkNavigator;
