import React from 'react';
import TechItem from "./TechItem";
import ReactTooltip from 'react-tooltip';

const TechList = (props) => {

    const {limit} = props;

    const techs = props.list.slice(0, limit).map((tech, i) => {
        return (
            <TechItem key={i} name={tech} className={props.itemStyle}/>
        );
    });

    const tip = props.list.slice(3, props.list.length).map((el) => el).join(", ");

    return (
        <div className={props.className}>
            {techs}
            {props.list.length > limit &&
            <>
                <TechItem key={limit} name={`+${props.list.length - limit}`} className={props.itemStyle} tip={tip}/>
                <ReactTooltip multiline arrowColor={"rgba(0,0,0,0)"} />
            </>
            }
        </div>
    );
};

export default TechList;
