import React from 'react';
import Header from "../components/Custom/Header";
import Footer from "../components/Custom/Footer";
import WorkSection from "../components/WorkSection";
import Navigation from "./navigation";
import Container from "../components/Container";
import {useParams} from "react-router-dom";
import workCategories from "../data/js/workCategories";
import ContactFloating from "../components/Custom/ContactFloating";

const Works = () => {

    const params = useParams();

    return (
        <Container>
            <ContactFloating src={Navigation.Work}/>
            <Header btn={"Home"} nav={`/${Navigation.Home}`}/>

            <WorkSection src={Navigation.Work} workCategory={(params.category !== undefined) ? params.category : workCategories.personal}/>

            <Footer/>
        </Container>
    );
};

export default Works;
