import React from 'react';
import styles from '../sass/appBar.module.scss'
import { useNavigate } from "react-router-dom";
import Navigation from "../pages/navigation";

const AppBar = () => {
    const navigate = useNavigate();

    return (
        <div className={styles.container}>
            <span className={styles.item} onClick={()=> navigate(`/${Navigation.Resume}`)}>Resume</span>
            <span className={styles.item} onClick={()=> navigate(`/${Navigation.Contact}`)}>Contact</span>
            <span className={styles.item} onClick={()=> navigate(`/${Navigation.About}`)}>About</span>
        </div>
    );
};

export default AppBar;
