import React, {useState} from 'react';
import styles from '../sass/work-section.module.scss'
import PrevItem from "./PrevItem";
import personalWorkData from "../data/js/personalWorkData";
import technology from "../data/js/technology";
import {personalFilteredData, academicFilteredData} from "../data/js/filteredData";
import FilterList from "./FilterList";
import Title from "./Custom/Title";
import workCategories from "../data/js/workCategories";
import academicWorkData from "../data/js/academicWorkData";
import {useNavigate} from "react-router-dom";
import Navigation from "../pages/navigation";
import ImageViewer from "./Custom/ImageViewer";

const WorkSection = (props) => {

    const navigate = useNavigate();

    const [personalFilter, setPersonalFilter] = useState(technology.All);
    const [academicFilter, setAcademicFilter] = useState(technology.All);
    const [workCategory, setWorkCategory] = useState(props.workCategory);

    const [imageViewer, setImageViewer] = useState({isVisible: false});

    const onPrevImgPress = (workImgs, imgId) => {

        let imgs = [];
        imgs.push(workImgs.mainPrev);
        for (const prev of workImgs.secPrev) imgs.push(prev.img)

        setImageViewer({
            isVisible: true,
            imgs,
            imgId
        });
    }

    const personalList = (workCategory !== workCategories.personal) ? null
        : personalFilteredData.techs.find((el) => el.name === personalFilter).projectIDs.map((id) => {
            const work = personalWorkData.find((el) => el.id === id)
            return (
                <PrevItem key={workCategories.personal + id} id={work.id} name={work.name} subTitle={work.subTitle}
                          mainPrev={work.prev.mainPrev} category={workCategory} isFeatured={work.featured}
                          src={props.src} externalUrl={work.externalUrl} brief={work.brief}
                          secPrev={work.prev.secPrev} techs={work.techs} github={work.github}
                          onImgSelect={(imgId) => onPrevImgPress(work.prev, imgId)}
                />
            );
        });

    const academicList = (workCategory !== workCategories.academic) ? null
        : academicFilteredData.techs.find((el) => el.name === academicFilter).projectIDs.map((id) => {
            const work = academicWorkData.find((el) => el.id === id)
            return (
                <PrevItem key={workCategories.academic + id} id={work.id} name={work.name} subTitle={work.subTitle}
                          mainPrev={work.prev.mainPrev} category={workCategory}
                          src={props.src} externalUrl={work.externalUrl} brief={work.brief}
                          secPrev={work.prev.secPrev} techs={work.techs} github={work.github}
                          onImgSelect={(imgId) => onPrevImgPress(work.prev, imgId)}
                />
            );
        });

    return (
        <div className={styles.workContainer}>
            <ImageViewer data={imageViewer}
                         onDismiss={() => setImageViewer({isVisible: false})}/>

            <div className={styles.workContainerHeader}>
                <Title title={"Work"}/>
                <p className={styles.headerBrief}>Here you can check both my personal and my academic work with
                    technology filtration.&nbsp;
                    <a onClick={() => navigate(`/${Navigation.Contact}`)}>Contact me</a> for more information.</p>

                <div className={styles.workCategory}>
                    <span
                        onClick={() => setWorkCategory(workCategories.personal)}
                        className={`${styles.left} ${(workCategory === workCategories.personal) ? styles.active : styles.inActive}`}>
                        {workCategories.personal}
                    </span>
                    <span
                        onClick={() => setWorkCategory(workCategories.academic)}
                        className={`${styles.right} ${(workCategory === workCategories.academic) ? styles.active : styles.inActive}`}>
                        {workCategories.academic}
                    </span>
                </div>
            </div>

            <div className={styles.itemsContainer}>
                {(workCategory === workCategories.personal)
                    ?
                    <>
                        <FilterList cat={workCategories.personal} list={personalFilteredData}
                                    activeFilter={personalFilter} onFilter={(filter) => setPersonalFilter(filter)}/>
                        <div className={styles.prevListContainer}>
                            {personalList}
                        </div>
                    </>
                    :
                    <>
                        <FilterList cat={workCategories.academic} list={academicFilteredData}
                                    activeFilter={academicFilter} onFilter={(filter) => setAcademicFilter(filter)}/>
                        <div className={styles.prevListContainer}>
                            {academicList}
                        </div>
                        <Title title={"Adding projects in progress..."}/>
                    </>
                }
            </div>
        </div>
    );
};

export default WorkSection;
