import React from 'react';
import styles from '../sass/work-section.module.scss'

const FilterItem = (props) => {

    const className = styles.filterItem + ((props.isActive) ? " " + styles.filterItemActive : "");

    return (
        <span className={className} onClick={() => props.onFilter(props.name)}>
            {`${props.name} (${props.count})`}
        </span>
    );
};

export default FilterItem;
