import React from 'react';
import styles from "../../sass/custom.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faArrowLeft} from "@fortawesome/free-solid-svg-icons";
import {useNavigate} from "react-router-dom";

const HeaderBtn = (props) => {
    const navigate = useNavigate();

    return (
        <div className={styles.headerBtn + " " + props.className} onClick={()=> navigate(props.nav)}>
            <FontAwesomeIcon className={styles.headerIcon} icon={faArrowLeft}/>
            <span>{props.btn.charAt(0).toUpperCase() + props.btn.slice(1)}</span>
        </div>
    );
};

export default HeaderBtn;
