import technology from "./technology";

export const personalFilteredData = {

    techs : [
        {
            name: technology.All,
            projectIDs: []
        },
        {
            name: technology.Python,
            projectIDs: []
        },
        {
            name: technology.Website,
            projectIDs: []
        },
        {
            name: technology.Game,
            projectIDs: []
        },
        {
            name: technology.App,
            projectIDs: []
        },
        {
            name: technology.React,
            projectIDs: []
        },
        {
            name: technology.ReactNative,
            projectIDs: []
        },
        {
            name: technology.Unity,
            projectIDs: []
        }
    ],

    AddProject: (tech, id) => {
        personalFilteredData.techs.find((el) => el.name === tech)?.projectIDs.push(id);
    }
};

