import React from 'react';
import ReactDOM from 'react-dom';
import './sass/app.scss';
import App from './App';
import {BrowserRouter, HashRouter} from "react-router-dom";
import {
    createHashRouter,
    RouterProvider
} from 'react-router-dom';
import personalWorkData from "./data/js/personalWorkData";
import academicWorkData from "./data/js/academicWorkData";
import {personalFilteredData, academicFilteredData} from "./data/js/filteredData";
import technology from "./data/js/technology";
import ScrollToTop from "./pages/ScrollToTop";
import MessageContainer from "./components/Custom/MessageContainer";

//Init Data
personalWorkData.forEach((work) => {
    personalFilteredData.AddProject(technology.All, work.id)
    work.techs.forEach((tech) => personalFilteredData.AddProject(tech, work.id));
});

academicWorkData.forEach((work) => {
    academicFilteredData.AddProject(technology.All, work.id)
    work.techs.forEach((tech) => academicFilteredData.AddProject(tech, work.id));
});

const router = createHashRouter([
    {
        path: "/*",
        element: <App />,
    }
]);

ReactDOM.render(
    <React.StrictMode>
        <MessageContainer />
        <RouterProvider router={router} />
    </React.StrictMode>,
    document.getElementById('root')
);
